import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-gift',
  templateUrl: './gift.component.html',
  styleUrls: ['./gift.component.scss']
})
export class GiftComponent implements OnInit {
  pageLoaded: boolean = false;
  firstBox: string = '';
  secondBox: string = '';
  thirdBox: string = '';
  fourthBox: string = '';
  giftForm: FormGroup = new FormGroup({});

  @Output()
  public messageOut: EventEmitter<messageType> = new EventEmitter()

  private selection = [
    'Triceratopsz',
    'Autó',
    'Motorka',
    'Cupákos',
    'Vicces kép',
    'Kép egy Zetorról',
    'Válasz egy kérdésre',
    'Zorro álarca',
    'Megszerelek valamit',
    'Elrontok valamit',
    'Vesénrúgok valakit',
    'Nyelvtörő',
    'Zokni',
    'Megindító film',
    'Tockos',
    'Elárulom Hannah Montana nevét'
  ]

  constructor(private httpClient: HttpClient) {}

  ngOnInit(): void {
    this.newSelection();
    this.giftForm = new FormGroup({
      name: new FormControl('', [Validators.required, Validators.minLength(3)]),
      mail: new FormControl('')
    })
    setTimeout(() => this.pageLoaded = true, 500);
  }

  private newSelection() {
    let index: number = Math.floor(Math.random() * this.selection.length)
    this.firstBox = this.selection[index];
    this.selection.splice(index, 1);
    index = Math.floor(Math.random() * this.selection.length)
    this.secondBox = this.selection[index];
    this.selection.splice(index, 1);
    index = Math.floor(Math.random() * this.selection.length)
    this.thirdBox = this.selection[index];
    this.selection.splice(index, 1);
    index = Math.floor(Math.random() * this.selection.length)
    this.fourthBox = this.selection[index];
    this.selection.splice(index, 1);
  }

  public sendToMe(message: string){
    console.log(message);
    if(this.giftForm.valid){
      const url = 'https://myespm.eu/christmas/gift-provider';
      const body = {
        data: this.giftForm.value,
        message,
      }

      const headers = new HttpHeaders({
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin': 'myespm.eu'
      })
      const jsonBody = JSON.stringify(body)
      console.log(this.giftForm);
      this.httpClient.post<any>(url, jsonBody).subscribe(
        data => {
          console.log(data);
          this.messageOut.emit('info')
        },
        error => {
          console.log(error);
          this.messageOut.emit('error')
        }
      )
    }else{
      this.messageOut.emit('error')
    }
  }
}

export type messageType = 'info' | 'error';
